* {
  margin: 0;
  padding: 0;
}

.app {
  width: 320px;
  margin: 5px auto;
  text-align: center;
  background-color: #ddd;
  border-radius: 5px;
}

.app-header {
  border-radius: 5px;
  text-transform: uppercase;
  font-size: 40px;
  background-color: #999;
  padding: 10px;
  color: white;
}

.app-title {
  font-size: 1.5em;
}

div::-webkit-scrollbar {
  width: 10px;
}

div::-webkit-scrollbar-track {
  background-color: #eeeeee;
  border-radius: 10px;
}

div::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #aaaaaa;
}

.chat-messages-container {
  height: 500px;
  font-size: 15px;
  overflow-y: scroll;
}

.chat-form {
  width: 100%;
  margin: 0 auto;
  padding: 5px 0px;
  border-radius: 5px;
  background-color: #999;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.width-80 {
  width: 80%;
}

.width-20 {
  width: 20%;
}

.chat-input {
  width: 95%;
  outline: none;
  min-height: 40px;
  border-radius: 5px;
  overflow-y: scroll;
  border: 1px solid white;
}

.message-box {
  width: 75%;
  padding: 5px;
  margin: 5px 2px;
  text-align: left;
  border-radius: 5px;
  background-color: white;
  overflow-wrap: break-word;
}

.message-box.left {
  background-color: white;

  float: left;
}

.message-box.right {
  background-color: skyblue;
  float: right;
}

.message-time {
  float: right;
  font-size: 12px;
  color: #777;
}

.button {
  margin: 5px 5px 0 0;
  border: none;
  font-size: 14px;
  font-weight: bold;
  border-radius: 5px;
  text-transform: uppercase;
  color: #ddd;
  padding: 8px;
  vertical-align: middle;
  background-color: blue;
}

.clearfix {
  display: table;
  clear: both;
  content: '';
}



